<template>
  <div id="app">
    <router-view/>
    <ul class="menu_list" v-if="menu_flag">
      <li v-bind:class="{li_pre : home_active}" @click="goto(1)">
        <i v-bind:class="[home_active?icon_home_pre:icon_home]"></i>
        <span class="icon_text">首页</span>
      </li>
      <li v-bind:class="{li_pre : shop_active}" @click="goto(2)">
        <i v-bind:class="[shop_active?icon_shop_pre:icon_shop]"></i>
        <span class="icon_text">商城</span>
      </li>
      <li v-bind:class="{li_pre : me_active}" @click="goto(3)">
        <i v-bind:class="[me_active?icon_me_pre:icon_me]"></i>
        <span class="icon_text">我的</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      menu_flag : false,
      home_active : false,
      shop_active : false,
      me_active : false,
      icon_home : 'icon_home',
      icon_home_pre : 'icon_home_pre',
      icon_shop : 'icon_shop',
      icon_shop_pre : 'icon_shop_pre',
      icon_me : 'icon_me',
      icon_me_pre : 'icon_me_pre',
    }
  },
watch:{
  $route(to,from){
    const val = to.path;
    switch (val){
      case '/':
        this.menu_flag  = true;
        this.home_active = true;
        this.shop_active = false;
        this.me_active = false;
        break;
      case '/personal_center':
        this.menu_flag  = true;
        this.me_active = true;
        this.home_active = false;
        this.me_active = false;
        break;
      case '/shop':
        this.menu_flag  = true;
        this.shop_active = true;
        this.home_active = false;
        this.shop_active = false;
        break;
      default:
        this.menu_flag  = false;
        return false;
    }
  }
},
  mounted(){
   const val = this.$route.path;
    switch (val){
      case '/':
        this.menu_flag  = true;
        this.home_active = true;
        break;
      case '/personal_center':
        this.menu_flag  = true;
        this.me_active = true;
        break;
      case '/shop':
        this.menu_flag  = true;
        this.shop_active = true;
        break;
      default:
        this.menu_flag  = false;
        return false;
    }
  },
  methods:{
    goto(i){
      switch (i){
        case 1:
          this.$router.push('/');
          this.home_active = true;
          this.shop_active = false;
          this.me_active = false;
          break;
        case 2:
          this.$router.push('/shop');
          this.shop_active = true;
          this.home_active = false;
          this.me_active = false;
          break;
        case 3:
          this.$router.push('/personal_center');
          this.me_active = true;
          this.home_active = false;
          this.shop_active = false;
          break;
        default:
          this.menu_flag = false;
          return false;
      }
    }
  }
}
</script>
<style scoped>
  .menu_list{
    width:100%;
    border-top:1px solid #ddd;
    overflow: hidden;
    position: fixed;
    bottom:0;
    left:0;
    background: #fff;
    z-index: 9;
  }
  .menu_list li{
    width: 33.33%;
    float:left;
    color:#A2A5B6;
    font-size:10px;
    padding:8px;
    box-sizing: border-box;
  }
  .menu_list .li_pre{
    color:#3B4157;
  }
  .icon_text{
    display: block;
    width:100%;
    text-align: center;
  }
  .icon_home,.icon_shop,.icon_me,.icon_home_pre,.icon_shop_pre,.icon_me_pre{
    display: inline-block;
    width:100%;
    height:22px;
  }
  .icon_home{
    background: url("assets/images/icon_home.png") center no-repeat;
    background-size:auto 100%;
  }
  .icon_shop{
    background: url("assets/images/icon_mall.png") center no-repeat;
    background-size:auto 100%;
  }
  .icon_me{
    background: url("assets/images/icon_me.png") center no-repeat;
    background-size:auto 100%;
  }
  .icon_home_pre{
    background: url("assets/images/icon_home_pre.png") center no-repeat;
    background-size:auto 100%;
  }
  /*.icon_shop_pre{*/
    /*background: url("assets/images/icon_mall_pre.png") center no-repeat;*/
    /*background-size:auto 100%;*/
  /*}*/
  .icon_me_pre{
    background: url("assets/images/icon_me_pre.png") center no-repeat;
    background-size:auto 100%;
  }

</style>



