<template>
  <div class="main">
    <p class="welcome_msg">{{welcome_msg}}</p>
    <p class="welcome_msg02">{{welcome_msg02}}</p>
    <ul class="login_list">
      <li>
        <input type="password" class="login_input" maxlength="20" placeholder="请输入密码" v-model="password" v-on:change="check_password">
      </li>
      <li>
        <input type="password" class="login_input" maxlength="20" placeholder="请再次输入密码" v-model="confirm_password" v-on:change="check_password">
      </li>
    </ul>
    <p v-bind:class="[password_flag?login_btn:login_btn_active]" @click="submit">提交</p>
    <p class="prompt" v-if="prompt_flag"><span>{{ prompt_text }}</span></p>
  </div>
</template>

<script>
  export default {
    name: "set_password",
    data(){
      return{
        welcome_msg:'请设置登录密码',//欢迎大标题
        welcome_msg02:'密码为6-20位，须包含数字及字母',//提示语
        password:'',
        confirm_password:'',
        login_btn:'login_btn',
        login_btn_active:'login_btn_active',
        password_flag:true,
        prompt_flag:false,
        prompt_text:""

      }
    },
    methods:{
      check_password(e){
        var that = this;
        var passval = e.srcElement.value;
        console.log(passval.length);
        if(passval.length < 6 || passval.length > 20){
          that.prompt_flag = true;
          that.prompt_text = '输入密码格式错误';
          that.password = '';
          that.confirm_password = '';
          that.password_flag = true;
          var s = setTimeout(function(){
            that.prompt_flag = false;
            that.prompt_text = '';
          },1500)
          return false;
        }
        if(this.password && this.confirm_password){
          if(this.password !== this.confirm_password){
            that.prompt_flag = true;
            that.prompt_text = '两次输入的密码不一致';
            that.password = '';
            that.confirm_password = '';
            that.password_flag = true;
            var s = setTimeout(function(){
              that.prompt_flag = false;
              that.prompt_text = '';
            },1500)
            return false;
          }else{
            that.password_flag = false;
          }
        }
      },
      submit(){

      }
    }
  }
</script>

<style scoped>

</style>



