<template>
    <div>
      <ul class="bank_list">
        <li class="cmb_bank">
          <span class="icon_bg">
            <i class="icon_bank"></i>
          </span>
          <span class="main_card">主卡</span>
          <ul class="bank_data_list">
            <li class="data_list_name">招商银行</li>
            <li class="data_list_type">储蓄卡</li>
            <li class="data_list_number">**** **** **** 8888</li>
          </ul>
        </li>
        <li class="abc_bank">
          <span class="icon_bg">
            <i class="icon_bank"></i>
          </span>
          <span class="main_card">主卡</span>
          <ul class="bank_data_list">
            <li class="data_list_name">招商银行</li>
            <li class="data_list_type">储蓄卡</li>
            <li class="data_list_number">**** **** **** 8888</li>
          </ul>
        </li>
        <li class="ccb_bank">
          <span class="icon_bg">
            <i class="icon_bank"></i>
          </span>
          <span class="main_card">主卡</span>
          <ul class="bank_data_list">
            <li class="data_list_name">招商银行</li>
            <li class="data_list_type">储蓄卡</li>
            <li class="data_list_number">**** **** **** 8888</li>
          </ul>
        </li>
        <li class="cebb_bank">
          <span class="icon_bg">
            <i class="icon_bank"></i>
          </span>
          <span class="main_card">主卡</span>
          <ul class="bank_data_list">
            <li class="data_list_name">招商银行</li>
            <li class="data_list_type">储蓄卡</li>
            <li class="data_list_number">**** **** **** 8888</li>
          </ul>
        </li>
      </ul>
      <p class="support_bank">支持的银行</p>
    </div>
</template>

<script>
    export default {
        name: "bank_card"
    }
</script>

<style scoped>
  .bank_list{
    width:100%;
    padding: 20px 15px 0;
    box-sizing: border-box;
  }
  .bank_list>li{
    width:100%;
    height:120px;
    border-radius: 4px;
    margin-bottom: 15px;
    padding:18px;
    box-sizing: border-box;
  }
  .icon_bg{
    display: block;
    width: 36px;
    height:36px;
    background: #fff;
    border-radius: 50%;
    margin-top:8px;
    float:left;
  }
  .main_card{
    display: block;
    width:38px;
    height:18px;
    line-height: 18px;
    text-align: center;
    background: rgba(255,255,255,.2);
    border-radius: 9px;
    color:#fff;
    float:right;
  }
  .icon_bank{
    display: block;
    width:24px;
    height:24px;
    margin:6px;
    background: url("../assets/images/logo_ghh.png") no-repeat;
    background-size:100%;
  }

  .bank_data_list{
    width:100%;
    color:#fff;
    padding-left:66px;
    box-sizing: border-box;
  }
  .data_list_name{
    font-size: 18px;
    margin-bottom:8px;
  }
  .data_list_type{
    font-size: 12px;
    margin-bottom:14px;
  }
  .data_list_number{
    font-size: 18px;
  }
  .support_bank{
    width:100%;
    padding:0 15px;
    box-sizing: border-box;
    color:#4A90E2;
    font-size: 14px;
    text-align: right;
  }
  /*招商银行*/
  .cmb_bank{
    background: linear-gradient(135deg, #F6B689 , #F384A7);
  }

  /*农业银行*/
  .abc_bank{
    background: linear-gradient(135deg, #87E5AC , #6AC5C5);
  }

  /*建设银行*/
  .ccb_bank{
    background: linear-gradient(135deg, #82CDF7 , #6EA6F8);
  }

  /*光大银行*/
  .cebb_bank{
    background: linear-gradient(135deg, #F8C85E , #F9A150);
  }
</style>



