<template>
    <div class="problem">
      <p class="problem_title" @click="toggle_answer"><span>{{problem_title}}</span><i v-bind:class="[arr_flag?arrow_top:arrow_down]"></i></p>
      <p class="problem_answer" v-if="answer_flag">{{ problem_answer }}</p>
    </div>
</template>

<script>
    export default {
      props:["p_answer","p_title"],
      name: "problem",
      data(){
          return {
            problem_title:this.p_title,
            problem_answer:this.p_answer,
            arrow_top:'icon_arrow_top',
            arrow_down:'icon_arrow_bottom',
            arr_flag:true,
            answer_flag:true
          }
      },
      methods:{
        toggle_answer(){
          this.arr_flag = !this.arr_flag;
          this.answer_flag = !this.answer_flag;
        }
      }
    }
</script>

<style scoped>
  .problem{
    width: 100%;
    line-height: 50px;
    font-size: 15px;
    color:#333;
    overflow: hidden;
  }
  .problem_title{
    width:100%;
    height: 50px;
    padding: 0 15px;
    box-sizing: border-box;
    font-size:15px;
    color:#333;
    border-bottom:1px solid #f0f0f0;
  }
  .problem_title span{
    display: inline-block;
    vertical-align: middle;
  }
  .problem_answer{
    width:100%;
    padding:10px 18px 15px;
    box-sizing: border-box;
    color:#999;
    font-size:13px;
    line-height: 26px;
    background:#F2F2F2;
  }
  .icon_arrow_bottom{
    display: inline-block;
    width: 16px;
    height: 16px;
    background: url("../assets/images/icon_up.png") no-repeat;
    background-size: 100%;
    float: right;
    margin-top:17px;
  }
  .icon_arrow_top{
    display: inline-block;
    width: 16px;
    height: 16px;
    background: url("../assets/images/icon_down.png") no-repeat;
    background-size: 100%;
    float: right;
    margin-top:17px;
  }
</style>



