<template>
    <div class="main">
      <img src="../assets/images/icon_success.png" alt="" class="icon_state">
      <p class="loaning">放款中</p>
      <p class="loan_amt">3000元</p>
      <p class="result_msg">预计15分钟放款成功，具体以银行到账为准！</p>
      <p class="result_btn">查看借款订单</p>
    </div>
</template>

<script>
    export default {
        name: "result"
    }
</script>

<style scoped>
  .icon_state{
    display: block;
    width:66px;
    margin:42px auto 24px;
  }
  .loaning{
    text-align: center;
    font-size:18px;
    color:rgba(51,51,51,1);
    margin:24px 0 15px;
  }
  .loan_amt{
    text-align: center;
    font-size:30px;
    color:rgba(51,51,51,1);
  }
  .result_msg{
    text-align: center;
    font-size:14px;
    color:rgba(153,153,153,1);
    margin:37px 0 20px;
  }
  .result_btn{
    width:100%;
    line-height: 45px;
    text-align: center;
    background: #4A90E2;
    border-radius: 2px;
    font-size:15px;
    color:#fff;
  }
</style>



