<template>
  <div>
    <p class="margin_top"></p>
    <ul class="account_security_list">
      <li>
        <span class="loan_list_title">{{ login_password }}</span>
        <i class="icon_arrow"></i>
      </li>
      <li>
        <span class="loan_list_title">{{ transaction_password }}</span>
        <i class="icon_arrow"></i>
      </li>
    </ul>
  </div>
</template>

<script>
    export default {
        name: "account_security",
      data(){
        return{
          login_password:'设置登录密码',
          transaction_password:'设置交易密码'

        }
      }
    }
</script>

<style scoped>
  .margin_top{
    width:100%;
    height: 10px;
    background: #f2f2f2;
  }
  .account_security_list{
    width:100%;
    padding:0 15px;
    box-sizing: border-box;
  }
  .account_security_list li{
    height:50px;
    padding:0 15px;
    box-sizing: border-box;
    border-bottom:1px solid #F0F0F0;
    font-size:15px;
    color:#333;
    line-height:50px;
    position:relative;
  }
  .icon_arrow{
    display: block;
    width:16px;
    height:16px;
    background: url("../assets/images/icon_next.png") no-repeat;
    background-size:100%;
    position:absolute;
    right:15px;
    top:50%;
    margin-top:-8px;
  }
</style>



