<template>
  <div class="verify">
    <p class="verify_title">验证手机号</p>
    <p class="verify_msg">已发送验证码短信到：180****2446</p>
    <div class="verify_get_code">
      <input type="text" class="login_input" placeholder="请输入验证码" v-model="verification_code">
      <span v-bind:class="[get_code_flag ? get_img_code :  get_img_code_dis]" @click="get_code">{{get_code_text}}</span>
    </div>
    <p class="prompt" v-if="prompt_flag"><span>{{ prompt_text }}</span></p>
    <p class="verify_submit">提交</p>
  </div>
</template>

<script>
    export default {
        name: "Verify_phone",
      data(){
          return{
            verification_code:'',
            get_code_flag:true,
            get_img_code:'get_img_code',
            get_img_code_dis:'get_img_code_dis',
            get_code_text:'获取验证码',
            prompt_flag:false,
            prompt_text:''


          }
      },
      mounted(){
        let that = this;
        this.get_code_flag = !this.get_code_flag;
        var count = 5;
        count--;
        this.get_code_text = count + 's';
        var timer = setInterval(function() {
          count--;
          if(count === 0) {
            that.get_code_flag = !this.get_code_flag;
            that.get_code_text = '重新获取';
            clearInterval(timer);
          } else {
            that.get_code_text = count + 's';
          }
        }, 1000)
      },
      methods:{
        get_code(){
          var that = this;
          if(that.get_code_flag){
            that.get_code_flag = !this.get_code_flag;
            var count = 10;
            count--;
            this.get_code_text = count + 's';
            var timer = setInterval(function() {
              count--;
              if(count === 0) {
                that.get_code_flag = !this.get_code_flag;
                that.get_code_text = '重新获取';
                clearInterval(timer);
              } else {
                that.get_code_text = count + 's';
              }
            }, 1000)
          }
          this.$ajax.post(this.global.sUrl+'api-n/notification-anon/codes', {
            params: {
              mobile: that.mobile_number
            }
          }).then(function (response) {
            that.sms_key = response.data.key;
            that.sms_number += 1;
            that.prompt_flag = true;
            that.prompt_text = '验证码已发送';
            var s = setTimeout(function(){
              that.prompt_flag = false;
              that.prompt_text = '';
            },1500)
          }).catch(function (error) {
            console.log(error);
          });

        }
      }
    }
</script>

<style scoped>
  .verify{
    width: 100%;
    box-sizing: border-box;
    padding:0 15px;
  }
  .verify_title{
    color:#333;
    font-size: 28px;
    margin-top:60px;
  }
  .verify_get_code{
    position: relative;
  }
  .verify_msg{
    color:#999;
    margin-top:5px;
  }
  .verify_get_code{
    margin-top:40px;
  }
  .verify_submit,.verify_submit_act{
    width:100%;
    height:45px;
    line-height: 45px;
    color:#fff;
    text-align: center;
    margin:40px auto;
    background: #E3E3E3;
    border-radius: 2px;
  }
  .verify_submit_act{
    background: #4A90E2;
  }
</style>



