<template>
    <div class="bg_f2f2f2">
      <p class="bank_msg">请绑定持卡人本人的借记卡</p>
      <ul class="bank_info_list">
        <li>
          <span class="info_list_title">持卡人</span>
          <input type="text" class="info_list_input">
          <i class="icon_msg"></i>
        </li>
        <li>
          <span class="info_list_title">银行卡号</span>
          <input type="text" class="info_list_input">
          <i class="icon_photo"></i>
        </li>
        <li>
          <span class="info_list_title">预留手机号</span>
          <input type="text" class="info_list_input">
        </li>
        <li>
          <span class="info_list_title">发卡行</span>
          <input type="text" class="info_list_input">
        </li>
        <li>
          <span class="info_list_title">卡类型</span>
          <input type="text" class="info_list_input">
        </li>
      </ul>
      <p class="next_step_act">下一步</p>
      <div class="shadow" v-if="shadow_flag"></div>
      <div class="popup" v-if="popup_flag">
        <p class="popup_title">{{popup_title}}</p>
        <p class="popup_text">{{ popup_text }}</p>
        <p class="popup_btn" @click="close_popup">知道了</p>
      </div>
    </div>
</template>

<script>
    export default {
        name: "add_bank_card",
      data(){
          return{
            popup_flag:true,
            popup_title:'持卡人说明',
            popup_text:'欢为保证资金交易安全，只能绑定认证用户本人的银行卡',
            shadow_flag:true
          }
      }
    }
</script>

<style scoped>
  .bg_f2f2f2{
    width:100%;
    height:100%;
    background: #F2F2F2;
  }
  .bank_msg{
    line-height: 42px;
    color:#999;
    font-size: 13px;
    box-sizing: border-box;
    padding:0 15px;
  }
  .bank_info_list{
    background: #fff;
    width:100%;
    color:#333;
    font-size:15px;
  }
  .bank_info_list li{
    width:100%;
    height:50px;
    border-bottom:1px solid #f0f0f0;
    box-sizing: border-box;
    padding:0 15px;
    position: relative;
  }
  .info_list_title{
    display: inline-block;
    width:90px;
    line-height: 50px;
  }
  .info_list_input{
    width:195px;
    height:50px;
    box-sizing: border-box;
    padding-right:20px;
  }
  .icon_msg,.icon_photo{
    width:14px;
    height:14px;
    background:url("../assets/images/icon_msg.png") no-repeat;
    background-size: 100%;
    position:absolute;
    top:50%;
    right:15px;
    margin-top:-7px;
  }
  .icon_photo{
    width:17px;
    background:url("../assets/images/icon_camera.png") no-repeat;
    background-size: 100%;
  }
  .next_step,.next_step_act{
    width:85%;
    height:45px;
    line-height: 45px;
    color:#fff;
    text-align: center;
    margin:25px auto;
    background: #E3E3E3;
    border-radius: 2px;
  }
  .next_step_act{
    background: #4A90E2;
  }


  .popup{
    width:270px;
    border-radius: 7px;
    overflow: hidden;
    position: fixed;
    top:20%;
    left:50%;
    margin-left:-135px;
  }
  .popup_title{
    width:100%;
    box-sizing: border-box;
    padding:30px 0;
    text-align: center;
    background: #fff;
    font-size:18px;
    color:#333;
  }
  .popup_text{
    width:100%;
    box-sizing: border-box;
    padding:0 20px 20px;
    font-size:14px;
    color:#999;
    line-height: 20px;
    background: #fff;
    margin-top:-15px;
    text-align: center;

  }
  .popup_btn{
    color:#108EE9;
    font-size:18px;
    text-align: center;
    padding:16px 0;
    background: #fff;
    border-top:1px solid #ddd;
  }
</style>



