<template>
  <div class="main">
    <p class="welcome_msg">{{welcome_msg}}</p>
    <p class="welcome_msg02">{{welcome_msg02}}</p>
    <ul class="login_list">
      <li>
        <input type="tel" class="login_input" maxlength="11" placeholder="请输入手机号码" v-model="mobile_number" v-on:change="check_mobile">

      </li>
      <li>
        <input type="text" class="login_input" placeholder="请输入验证码" v-model="verification_code">
        <span v-bind:class="[get_code_flag ? get_img_code :  get_img_code_dis]" @click="get_code">{{get_code_text}}</span>
      </li>
      <li v-if="sms_number > 3">
        <input type="text" class="login_input" placeholder="请输入图形验证码" v-model="img_code">
        <img src="../assets/images/img_code.jpeg" alt="" class="img_imgcode">
      </li>
    </ul>
    <p class="login_btn login_btn_active" @click="next_step">下一步</p>
    <p class="prompt" v-if="prompt_flag"><span>{{ prompt_text }}</span></p>
  </div>
</template>
<script>
    export default {
      name: "forget_password",
      data(){
        return{
          welcome_msg:'忘记密码',//欢迎大标题
          welcome_msg02:'请输入您的手机号以查找您的账号',//提示语
          mobile_number:'',
          verification_code:'',
          prompt_text:'请输入手机号',//提示语
          prompt_flag:false,//错误提示标识
          get_code_text:'获取验证码',
          get_code_flag:true,
          get_img_code:'get_img_code',
          get_img_code_dis:'get_img_code_dis',
          sms_key:'',//发送验证码传回来的key
          sms_number:0,//发送验证码次数
          img_code:''
        }
      },
      methods:{
        get_code:function() {
          var that = this
          if (that.mobile_number) {
            if (that.get_code_flag) {
              that.get_code_flag = !this.get_code_flag;
              var count = 10;
              count--;
              this.get_code_text = count + 's';
              var timer = setInterval(function () {
                count--;
                if (count === 0) {
                  that.get_code_flag = !this.get_code_flag;
                  that.get_code_text = '重新发送';
                  clearInterval(timer);
                } else {
                  that.get_code_text = count + 's';
                }
              }, 1000)
            }
            this.$ajax.post(that.global.sUrl+'api-n/notification-anon/sendmsgbyid', {
              params: {
                templetId:'SMS_140055034',
                mobile: that.mobile_number
              }
            }).then(function (response) {
              that.sms_key = response.data.key;
              that.sms_number += 1;
              that.prompt_flag = true;
              that.prompt_text = '验证码已发送';
              var s = setTimeout(function () {
                that.prompt_flag = false;
                that.prompt_text = '';
              }, 1500)
            }).catch(function (error) {
              console.log(error);
            });
          }else{
            that.prompt_flag = true;
            that.prompt_text = '请输入手机号';
            var s = setTimeout(function () {
              that.prompt_flag = false;
              that.prompt_text = '';
            }, 1500)
          }
        },
        check_mobile(e){
          var that = this;
          var val = e.srcElement._value;
          if(val === '') {
            that.prompt_flag = true;
            that.mobile_number = '';
            that.prompt_text = '请输入注册手机号';
            var s = setTimeout(function(){
              that.prompt_flag = false;
              that.prompt_text = '';
            },1500)
            return false;
          }
          if(!/^0?1[3|4|5|7|8][0-9]\d{8}$/.test(val)) {
            that.prompt_flag = true;
            that.mobile_number = '';
            that.prompt_text = '手机号码输入错误';
            var s = setTimeout(function(){
              that.prompt_flag = false;
              that.prompt_text = '';
            },1500)
            return false;
          }
          return true;
        },
        next_step(){
          var that = this;
          if(!that.mobile_number){
            that.prompt_flag = true;
            that.prompt_text = '请输入手机号码';
            var s = setTimeout(function(){
              that.prompt_flag = false;
              that.prompt_text = '';
            },1500)
            return false;
          }else{

            if(that.verification_code){
            // 校验验证码
            that.$ajax.get(that.global.sUrl+'api-n/notification-anon/internal/phone',{
              params: {
                key: that.sms_key,
                code: that.verification_code
              }
            }).then(function(res){
              console.log(res);
              if(that.mobile_number !== res.data.mobile){
                that.prompt_flag = true;
                that.prompt_text = '验证码有误';
                var s = setTimeout(function(){
                  that.prompt_flag = false;
                  that.prompt_text = '';
                },1500)
                return false;
              }

            }).catch(function(error){
              console.log(error);
            })
            }else{
              that.prompt_flag = true;
              that.prompt_text = '请输入验证码';
              var s = setTimeout(function(){
                that.prompt_flag = false;
                that.prompt_text = '';
              },1500)
              return false;
            }
          }
        }
      }
    }
</script>

<style scoped>
  .prompt{
    width:100%;
    position:fixed;
    left:0;
    top:50%;
    color:#fff;
    font-size: 15px;
    text-align: center;
  }
  .prompt span{
    display: inline-block;
    padding:15px;
    background: #303030;
    border-radius:5px;
  }
</style>



