<template>
  <div>
    <ul class="help_title_list">
      <li v-bind:class="[ zc_flag? active_li:active_li_dis]" @click="problem_list_value(1)">注册</li>
      <li v-bind:class="[ rz_flag? active_li:active_li_dis]" @click="problem_list_value(2)">认证</li>
      <li v-bind:class="[ sx_flag? active_li:active_li_dis]" @click="problem_list_value(3)">授信</li>
      <li v-bind:class="[ jk_flag? active_li:active_li_dis]" @click="problem_list_value(4)">借款</li>
      <li v-bind:class="[ hk_flag? active_li:active_li_dis]" @click="problem_list_value(5)">还款</li>
      <li v-bind:class="[ zh_flag? active_li:active_li_dis]" @click="problem_list_value(6)">账户</li>
    </ul>
    <div v-for="data in problem_list" is="problems" :p_title="data.title" :p_answer="data.answer"></div>
  </div>
</template>

<script>
  import problems from "./problem.vue";
    export default {
      name: "help",
      components:{problems},
      data(){
          return{
            problem_list:this.global.zc_problem,
            zc_flag:true,
            rz_flag:false,
            sx_flag:false,
            jk_flag:false,
            hk_flag:false,
            zh_flag:false,
            active_li:'active_li',
            active_li_dis:'active_li_dis'

          }
      },
      methods:{
        problem_list_value(i){
          switch (i){
            case 1:
              this.zc_flag = true;
              this.rz_flag = false;
              this.sx_flag = false;
              this.jk_flag = false;
              this.hk_flag = false;
              this.zh_flag = false;
              break;
            case 2:
              this.zc_flag = false;
              this.rz_flag = true;
              this.sx_flag = false;
              this.jk_flag = false;
              this.hk_flag = false;
              this.zh_flag = false;
              break;
            case 3:
              this.zc_flag = false;
              this.rz_flag = false;
              this.sx_flag = true;
              this.jk_flag = false;
              this.hk_flag = false;
              this.zh_flag = false;
              break;
            case 4:
              this.zc_flag = false;
              this.rz_flag = false;
              this.sx_flag = false;
              this.jk_flag = true;
              this.hk_flag = false;
              this.zh_flag = false;
              break;
            case 5:
              this.zc_flag = false;
              this.rz_flag = false;
              this.sx_flag = false;
              this.jk_flag = false;
              this.hk_flag = true;
              this.zh_flag = false;
              break;
            case 6:
              this.zc_flag = false;
              this.rz_flag = false;
              this.sx_flag = false;
              this.jk_flag = false;
              this.hk_flag = false;
              this.zh_flag = true;
              break;
            default:
              return false;
          }

        }
      }
    }
</script>

<style scoped>
  .help_title_list{
    color:#333;
    text-align: center;
    line-height:42px;
    font-size:13px;
    border:1px solid #f0f0f0;
    overflow: hidden;
  }
  .help_title_list li{
    width:16.6%;
    float: left;
    border-bottom:2px solid #fff;
  }
  /*.help_title_list li:first-child{*/
    /*color:#4A90E2;*/
    /*border-bottom:2px solid #4A90E2;*/
  /*}*/
  .help_title_list .active_li{
    color:#4A90E2;
    border-bottom:2px solid #4A90E2;
  }

</style>



