<template>
    <div class="bg_f2f2f2">
      <ul class="payment_list">
        <li v-for="data in payment_list_data">
          <span class="payment_list_date">{{ data.date }}</span>
          <span class="payment_list_money">{{ data.money }}</span>
          <span v-bind:class="{payment_list_state : list_state_flag,'color_4A90E2':data.state === 2,'color_999':data.state === 3}">{{ data.state_text }}</span>
        </li>
      </ul>
    </div>
</template>

<script>
    export default {
        name: "payment_history",
      data(){
        return{
          list_state_flag : true,
          payment_list_data : [
            {date:'2018-07-12',money:'3000.00',state:2,state_text:'还款中'},
            {date:'2018-07-12',money:'3000.00',state:3,state_text:'还款失败'},
            {date:'2018-07-12',money:'3000.00',state:4,state_text:'还款成功'}
          ]
        }
      }
    }
</script>

<style scoped>
  .bg_f2f2f2{
    width:100%;
    height:100%;
    background: #f2f2f2;
  }
  .payment_list{
    width:100%;
    background: #fff;
    color:#333;
  }
  .payment_list li{
    line-height: 50px;
    padding:0 15px;
    box-sizing: border-box;
    font-size: 15px;
    border-bottom: 1px solid #f0f0f0;
    overflow: hidden;
    position: relative;
  }
  .payment_list li span{
    display: inline-block;
  }
  .payment_list_date{
    float: left;
  }
  .payment_list_state{
    float: right;
  }
  .payment_list_money{
    width: 100%;
    height: 100%;
    text-align: center;
    position:absolute;
    top:0;
    left:0;
  }
  .color_4A90E2{
    color:#4A90E2;
  }
  .color_999{
    color:#999;
  }
</style>



