<template>
    <div class="loan">
      <ul class="loan_list">
        <li>
          <span class="loan_list_title" >借款金额</span>
          <input type="text" placeholder="单次最低借款金额3000元" class="loan_list_input" v-model="amount" v-on:change="check_money">
        </li>
        <li @click="get_bank(2)">
          <span class="loan_list_title">借款用途</span>
          <p class="loan_list_select">{{ purpose }}</p>
          <i class="icon_arrow"></i>
        </li>
        <li @click="get_bank(3)">
          <span class="loan_list_title">借款期限</span>
          <p class="loan_list_select">{{ time_limit }}</p>
          <i class="icon_arrow"></i>
        </li>
        <li>
          <span class="loan_list_title">还款日</span>
          <p class="loan_list_select">每月{{ repayment_day }}日<span>(以实际放款为准)</span></p>
          <i class="icon_todo" @click="open_todo"></i>
        </li>
        <li @click="get_bank(4)">
          <span class="loan_list_title">还款来源</span>
          <p class="loan_list_select">{{ source }}</p>
          <i class="icon_arrow"></i>
        </li>
      </ul>
      <p class="receipt_account" @click="get_bank(6)">
        <span class="loan_list_title">收款账户</span>
        <i v-bind:class="[icon_bank]"></i>
        <span>{{ bank_name }}</span>
        <i class="icon_arrow"></i>
      </p>
      <p class="risk_tip">【风险提示】本平台不向在校学生提供服务，借款不允许用于投资！请您根据还款能力理性借款，准时还款，以免影响您的信用！</p>
      <p class="next_step">下一步</p>
      <div class="shadow" v-if="shadow_flag"></div>
      <div class="popup" v-if="popup_flag">
        <p class="popup_title">{{popup_title}}</p>
        <p class="popup_text" v-if="popup_text_flag">{{ popup_text }}</p>
        <p class="popup_btn" @click="close_popup">知道了</p>
      </div>
      <div class="selects" v-if="selects_flag">
        <p class="selects_title">{{ selects_title }}</p>
        <ul class="selects_list">
          <li v-for="(data,index) in select_list_data" @click="get_val(index)">
            {{ data.data_name }}
          </li>
        </ul>
        <p class="selsects_cancel" @click="close_selects">取消</p>
      </div>
      <div class="selects" v-if="selects2_flag">
        <p class="selects_title">{{ selects_title }} <span @click="goto_allbank">支持的银行</span></p>
        <ul class="selects_list selects2_list">
          <li>
            <img src="../assets/images/logo_js.png" alt="" class="icon_bank">
            <span>建设银行 借记卡（9215）</span>
          </li>
        </ul>
        <p class="selsects_cancel" @click="close_selects">取消</p>
      </div>
    </div>
</template>

<script>
    export default {
        name: "loan",
      data () {
        return {
          amount:'',//借款额度
          purpose:'请选择',//借款用途
          time_limit:'请选择',//借款期限
          repayment_day:'12',//还款日
          source:'请选择',//还款来源
          icon_bank:'logo_ghh',//银行卡图标样式
          bank_name:'招商银行（9215）',//银行卡名称
          popup_title:'单次最低借款金额3000元',//弹层标题
          popup_text:'您的每次借款，还款日都将根据实际放款日期固定，如在1日借款，则后续还款日为每月1日；如在29-31日借款，则还款日为每月28日。 例如，您本次借款的实际放款日期为7月16日，则第一次还款日期为8月16日，以此类推。 ',//
          shadow_flag:false,//遮罩标识
          popup_flag:false,//弹层标识
          popup_text_flag:false,//弹层中提示信息标识
          selects_flag:false,//通用选项弹层标识
          selects2_flag:false,//收款银行卡选项弹层标识
          selects_title:'',//选项弹层标题
          select_list_data:[],//选项弹层数据
          numbers:0

        }
      },
      methods:{
        close_popup:function(){
          this.shadow_flag = false;
          this.popup_flag = false;
        },
        open_todo:function(){
          this.shadow_flag = true;
          this.popup_flag = true;
          this.popup_text_flag = true;
          this.popup_title = '还款日说明';
        },
        check_money(){
          if(this.amount < 3000){
            this.shadow_flag = true;
            this.popup_flag = true;
            this.popup_title = '单次最低借款金额3000元';
            this.amount = '';
          }
        },
        get_bank(t){
          var that = this;
          that.shadow_flag = true;
          that.numbers = t;
          switch (t){
            case 2:
              that.selects_flag = true;
              that.selects_title = '请选择借款用途';
              that.select_list_data = that.global.usage_loan;
              break;
            case 3:
              that.selects_flag = true;
              that.selects_title = '请选择借款期限';
              that.select_list_data = that.global.term_loan;
              break;
            case 4:
              that.selects_flag = true;
              that.selects_title = '请选择还款来源';
              that.select_list_data = that.global.source_loan;
              break;
            case 6:
              this.$ajax.post(that.global.sUrl+'17/getUserBankList',{
                params:{
                  token:'2tgug23veghvegh2veghu2vghev'
                }
              }).then(function (response) {
                that.selects2_flag = true;
                that.selects_title = '请选择收款账户';
                console.log(response);
              }).catch(function (error) {
                console.log(error);
              });
              break;
          }
        },
        goto_allbank(){
          this.$router.push('/allow_bank')
        },
        close_selects(){
          this.selects_flag = false;
          this.selects2_flag = false;
          this.shadow_flag = false;
        },
        get_val(i){
          var t = this.numbers;
          switch (t){
            case 2:
              this.purpose = this.select_list_data[i].data_name;
              this.shadow_flag = false;
              this.selects_flag = false;
              break;
            case 3:
              this.time_limit = this.select_list_data[i].data_name;
              this.shadow_flag = false;
              this.selects_flag = false;
              break;
            case 4:
              this.source = this.select_list_data[i].data_name;
              this.shadow_flag = false;
              this.selects_flag = false;
              break;
            // case 6:
            //   this.purpose = this.select_list_data[i].data_name;
            //   break;

          }
        }
      }
    }
</script>

<style scoped>
  .loan{
    width:100%;
    height:100%;
    background: #F2F2F2;
  }
  .loan_list{
    width:100%;
    background: #fff;
  }
  .loan_list li{
    height:50px;
    padding:0 15px;
    box-sizing: border-box;
    border-top:1px solid #F0F0F0;
    font-size:15px;
    color:#333;
    line-height:50px;
    position:relative;
  }
  .loan_list_title{
    display: inline-block;
    width:26%;
  }
  .loan_list_input{
    font-size:15px;
    width:180px;
  }
  .loan_list_select{
    display: inline-block;
    width:180px;
  }
  .loan_list_select span{
    color:#999;
    font-size:13px;
    margin-left:8px;
  }
  .icon_arrow{
    display: block;
    width:16px;
    height:16px;
    background: url("../assets/images/icon_next.png") no-repeat;
    background-size:100%;
    position:absolute;
    right:15px;
    top:50%;
    margin-top:-8px;
  }
  .icon_todo{
    display: block;
    width:14px;
    height:14px;
    background: url("../assets/images/icon_todo.png") no-repeat;
    background-size:100%;
    position:absolute;
    right:15px;
    top:50%;
    margin-top:-7px;
  }
  .receipt_account{
    height:50px;
    padding:0 15px;
    box-sizing: border-box;
    font-size:15px;
    color:#333;
    line-height:50px;
    position:relative;
    margin-top:10px;
    background: #fff;
  }
  .receipt_account span{
    display: inline-block;
    vertical-align: middle;
  }
  .logo_ghh{
    display: inline-block;
    width:20px;
    height:20px;
    background: url("../assets/images/logo_ghh.png") no-repeat;
    background-size:100%;
    vertical-align: middle;
    margin-right:5px;
  }
  .risk_tip{
    padding:0 15px;
    box-sizing: border-box;
    line-height: 18px;
    color:#999;
    font-size:13px;
    margin-top:32px;
  }
  .next_step{
    width:95%;
    margin:36px auto;
    text-align: center;
    font-size:15px;
    line-height: 45px;
    color:#fff;
    background: #E3E3E3;
    border-radius: 2px;
  }
  .popup{
    width:270px;
    border-radius: 7px;
    overflow: hidden;
    position: fixed;
    top:20%;
    left:50%;
    margin-left:-135px;
  }
  .popup_title{
    width:100%;
    box-sizing: border-box;
    padding:30px 0;
    text-align: center;
    background: #fff;
    font-size:18px;
    color:#333;
  }
  .popup_text{
    width:100%;
    box-sizing: border-box;
    padding:0 20px 20px;
    font-size:14px;
    color:#999;
    line-height: 20px;
    background: #fff;
    margin-top:-15px;

  }
  .popup_btn{
    color:#108EE9;
    font-size:18px;
    text-align: center;
    padding:16px 0;
    background: #fff;
    border-top:1px solid #ddd;
  }
  .selects{
    width:100%;
    background: #F2F2F2;
    text-align: center;
    color:#333;
    line-height: 50px;
    position: fixed;
    left:0;
    bottom:0;
  }
  .selects .selects_title{
    color:#999;
    background: #fff;
    position:relative;
  }
  .selects_list{
    background: #fff;
    margin-bottom:10px;
  }
  .selects_list li{
    border-top:1px solid #F0F0F0;
  }
  .selsects_cancel{
    background: #fff;
  }
  .selects_title span{
    color:#4A90E2;
    position:absolute;
    right:15px;
    top:0;
  }
  .icon_bank{
    display: inline-block;
    width:20px;
    height:20px;
    vertical-align: middle;

  }
  .selects2_list li{
    text-align: left;
    padding-left:15px;
    box-sizing: border-box;
  }
  .selects2_list li>span{
    display: inline-block;
    vertical-align: middle;

  }
</style>



