import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'//首页
import message_center from '@/components/message_center'//消息中心
import message_details from '@/components/message_details'//消息详情
import code_login from '@/components/code_login'//手机验证码登陆
import account_login from '@/components/account_login'//账号密码登陆
import set_password from '@/components/set_password'//设置登陆密码
import register from '@/components/register'//注册
import forget_password from '@/components/forget_password'//忘记密码验证
import shop from '@/components/shop'//商城
import personal_center from '@/components/personal_center'//个人中心
import loan from '@/components/loan'//借款
import select_bank from '@/components/select_bank'//选择银行
import allow_bank from '@/components/allow_bank'//支持银行
import loan_identify from '@/components/loan_identify'//确认借款订单
import repayment_plan from '@/components/repayment_plan'//查看还款计划
import clause from '@/components/clause'//协议详情（所有条款均用这个组件）
import result from '@/components/result'//结果页（所有结果页面）
import result2 from '@/components/result2'//结果页（所有结果页面）
import repayment from '@/components/repayment'//还款页面
import loan_record from '@/components/loan_record'//借款记录
import loan_details from '@/components/loan_record'//借款详情
import payment_history from '@/components/payment_history'//还款记录
import bank_card from '@/components/bank_card'//银行卡
import add_bank_card from '@/components/add_bank_card'//添加银行卡
import validate_phone from '@/components/validate_phone'//验证手机
import help from '@/components/help'//帮助中心
import deal_password_validate from '@/components/deal_password_validate'//设置交易密码验证
import set_deal_password from '@/components/set_deal_password'//设置交易密码
import set_of from '@/components/set_of'//设置
import account_security from '@/components/account_security'//账号安全
import personal_home from '@/components/personal_home'//账号安全
import Verify_phone from '@/components/Verify_phone'//验证手机号


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/code_login',
      name: 'code_login',
      component: code_login
    },
    {
      path: '/account_login',
      name: 'account_login',
      component: account_login
    },
    {
      path: '/set_password',
      name: 'set_password',
      component: set_password
    },
    {
      path: '/register',
      name: 'register',
      component: register
    },
    {
      path: '/forget_password',
      name: 'forget_password',
      component: forget_password
    },
    {
      path: '/shop',
      name: 'shop',
      component: shop
    },
    {
      path: '/personal_center',
      name: 'personal_center',
      component: personal_center
    },
    {
      path: '/loan',
      name: 'loan',
      component: loan
    },
    {
      path: '/select_bank',
      name: 'select_bank',
      component: select_bank
    },
    {
      path: '/allow_bank',
      name: 'allow_bank',
      component: allow_bank
    },
    {
      path: '/loan_identify',
      name: 'loan_identify',
      component: loan_identify
    },
    {
      path: '/repayment_plan',
      name: 'repayment_plan',
      component: repayment_plan
    },
    {
      path: '/clause',
      name: 'clause',
      component: clause
    },
    {
      path: '/result',
      name: 'result',
      component: result
    },
    {
      path: '/result2',
      name: 'result2',
      component: result2
    },
    {
      path: '/repayment',
      name: 'repayment',
      component: repayment
    },
    {
      path: '/loan_record',
      name: 'loan_record',
      component: loan_record
    },
    {
      path: '/loan_details',
      name: 'loan_details',
      component: loan_details
    },
    {
      path: '/payment_history',
      name: 'payment_history',
      component: payment_history
    },
    {
      path: '/bank_card',
      name: 'bank_card',
      component: bank_card
    },
    {
      path: '/add_bank_card',
      name: 'add_bank_card',
      component: add_bank_card
    },
    {
      path: '/validate_phone',
      name: 'validate_phone',
      component: validate_phone
    },
    {
      path: '/help',
      name: 'help',
      component: help
    },
    {
      path: '/deal_password_validate',
      name: 'deal_password_validate',
      component: deal_password_validate
    },
    {
      path: '/set_deal_password',
      name: 'set_deal_password',
      component: set_deal_password
    },
    {
      path: '/set_of',
      name: 'set_of',
      component: set_of
    },
    {
      path: '/message_center',
      name: 'message_center',
      component: message_center
    },
    {
      path: '/account_security',
      name: 'account_security',
      component: account_security
    },
    {
      path: '/message_details',
      name: 'message_details',
      component: message_details
    },
    {
      path: '/personal_home',
      name: 'personal_home',
      component: personal_home
    },
    {
      path: '/Verify_phone',
      name: 'Verify_phone',
      component: Verify_phone
    },
  ]
})





