<template>
    <div class="main">
      <p class="welcome_msg">{{welcome_msg}}</p>
      <ul class="login_list">
        <li>
          <input type="number" class="login_input" maxlength="18" placeholder="请输入认证身份证号" v-model="idNumber">
        </li>
        <li>
          <input type="tel" class="login_input" maxlength="11" placeholder="请输入手机号码" v-model="mobile_number" v-on:change="check_mobile">
          <i class="clear_input" v-if="mobile_number" @click="clear_input"></i>
        </li>
        <li>
          <input type="text" class="login_input" placeholder="请输入验证码" v-model="verification_code">
          <span v-bind:class="[get_code_flag ? get_img_code :  get_img_code_dis]" @click="get_code">{{get_code_text}}</span>
        </li>
        <li v-if="sms_number > 3">
          <input type="text" class="login_input" placeholder="请输入图形验证码" v-model="img_code">
          <img src="../assets/images/img_code.jpeg" alt="" class="img_imgcode">
        </li>
      </ul>
      <p class="login_btn login_btn_active">下一步</p>
      <p class="prompt" v-if="prompt_flag"><span>{{ prompt_text }}</span></p>
    </div>
</template>

<script>
    export default {
        name: "deal_password_validate",
      data () {
        return {
          welcome_msg:'设置交易密码',//欢迎大标题
          welcome_msg02:'请输入您的手机号进行登录或注册操作',//提示语
          idNumber:'',//身份证号
          mobile_number: '',//手机号
          verification_code:'',//验证码
          img_code:'',//图形验证码
          get_code_text:'获取验证码',
          get_code_flag:true,
          get_img_code:'get_img_code',
          get_img_code_dis:'get_img_code_dis',
          sms_key:'',//发送验证码传回来的key
          sms_number:0,//发送验证码次数
          prompt_text:'请输入手机号',//提示语
          prompt_flag:false,//错误提示标识
        }
      },
      methods:{
        check_mobile:function(value){
          var that = this;
          var val = value.srcElement._value;
          if(val === '') {
            that.prompt_flag = true;
            that.mobile_number = '';
            that.prompt_text = '请输入注册手机号';
            var s = setTimeout(function(){
              that.prompt_flag = false;
              that.prompt_text = '';
            },1500)
            return false;
          }
          if(!/^0?1[3|4|5|7|8][0-9]\d{8}$/.test(val)) {
            that.prompt_flag = true;
            that.mobile_number = '';
            that.prompt_text = '手机号码输入错误';
            var s = setTimeout(function(){
              that.prompt_flag = false;
              that.prompt_text = '';
            },1500)
            return false;
          }
          return true;
        },
        get_code:function(){
          var that = this
          if(that.mobile_number){
            if(that.get_code_flag){
              that.get_code_flag = !this.get_code_flag;
              var count = 10;
              count--;
              this.get_code_text = count + 's';
              var timer = setInterval(function() {
                count--;
                if(count === 0) {
                  that.get_code_flag = !this.get_code_flag;
                  that.get_code_text = '重新发送';
                  clearInterval(timer);
                } else {
                  that.get_code_text = count + 's';
                }
              }, 1000)
            }
            this.$ajax.post(this.global.sUrl+'api-n/notification-anon/codes', {
              params: {
                mobile: that.mobile_number
              }
            }).then(function (response) {
              that.sms_key = response.data.key;
              that.sms_number += 1;
              that.prompt_flag = true;
              that.prompt_text = '验证码已发送';
              var s = setTimeout(function(){
                that.prompt_flag = false;
                that.prompt_text = '';
              },1500)
            }).catch(function (error) {
              console.log(error);
            });
          }else{
            that.prompt_flag = true;
            that.prompt_text = '请输入手机号';
            var s = setTimeout(function () {
              that.prompt_flag = false;
              that.prompt_text = '';
            }, 1500)
          }
        },
        clear_input:function(){
          this.mobile_number = "";
        },
      }
    }
</script>

<style scoped>

</style>



