            msg_date:'2018-07-18 14:49:02',
            details_text:'<p>在APP更新功能后，运营人员需要为此次增添的功能提 供资源。可以是运营人员准备内容，也可以是运营人员 邀请用户产出内容。比如，陌陌上线了短视频功能，则 需要填充一些短视频，新浪微博上线了微博故事功能， 对用户进行了邀请。 得到APP刚开始是为用户提供电子书阅读，在2.0版本 后，才上线了付费订阅功能。在得到APP新功能上线之 前，运营人员策划了付费订阅内容，并对讲师进行沟通 邀请他们入驻得到APP，为APP新功能提供了资源。</p><br><p>在APP更新功能后，运营人员需要为此次增添的功能提 供资源。， 。，，，， 。 ，. ，。 ，， ，。</p>'
          }
      },
      methods:{

      }
    }
</script>

<style scoped>
  h2{
    color:#333;
    font-size: 18px;
    text-align: center;
    padding: 0 30px;
    margin-top:20px;
    line-height: 25px;
  }
  .date_time{
    width:100%;
    font-size:12px;
    color:rgba(153,153,153,1);
    text-align: center;
    margin-top:5px;
  }
  .details_text{
    width:100%;
    padding: 0 15px;
    box-sizing: border-box;
    color:#666;
    font-size: 14px;
    line-height: 26px;
    margin-top:15px;
  }
  .details_text p{
    margin-top:10px;
  }
</style>



