<template>
    <div class="deal">
      <p class="deal_title">设置交易密码</p>
      <p class="deal_msg">{{ deal_msg }}</p>
      <ul class="password_list">
        <li v-bind:class="[input_length === 1? spot_act : spot_act2]"><i class="spot" v-if="input_length > 0"></i></li>
        <li v-bind:class="[input_length === 2? spot_act : spot_act2]"><i class="spot" v-if="input_length > 1"></i></li>
        <li v-bind:class="[input_length === 3? spot_act : spot_act2]"><i class="spot" v-if="input_length > 2"></i></li>
        <li v-bind:class="[input_length === 4? spot_act : spot_act2]"><i class="spot" v-if="input_length > 3"></i></li>
        <li v-bind:class="[input_length === 5? spot_act : spot_act2]"><i class="spot" v-if="input_length > 4"></i></li>
        <li v-bind:class="[input_length === 6? spot_act : spot_act2]"><i class="spot" v-if="input_length > 5"></i></li>

        <li class="password_list_input">
          <input type="tel" class="list_input" maxlength="6" v-model="deal_password" v-on:input="password_change">
        </li>
      </ul>
      <p>{{ deal_password }}</p>
      <p class="prompt" v-if="prompt_flag"><span>{{ prompt_text }}</span></p>
    </div>
</template>

<script>
    export default {
        name: "set_deal_password",
      data(){
          return{
            deal_password:'',
            input_length :0,
            spot:'spot',
            spot_act:'spot_act',
            spot_act2:'spot_act2',
            deal_msg:'请输入6位交易密码',
            first_pass:'',
            second_pass:'',
            prompt_flag:false,
            prompt_text:''
          }
      },
      methods:{
          password_change(i){
            let that = this;
            this.input_length = i.target.value.length;
            if(i.target.value.length === 6){
              if(this.first_pass){
                this.second_pass = this.deal_password;
                if(this.second_pass === this.first_pass){
                  this.prompt_flag = true;
                  this.prompt_text = '设置完成'

                  //请求后台


                }else{
                  this.prompt_flag = true;
                  this.prompt_text = '两次密码不一致,请重新输入';
                  var s = setTimeout(function(){
                    that.prompt_flag = false;
                    that.prompt_text = '';
                    that.deal_password = '';
                    that.deal_msg = '请输入6位交易密码';
                    that.input_length = 0;
                    that.first_pass = '';
                    that.second_pass = '';
                  },1500)
                }
              }else{
                this.first_pass = this.deal_password;
                this.deal_password = '';
                this.deal_msg = '请再次输入6位交易密码';
                this.input_length = 0;

              }
            }
          }
      }
    }
</script>

<style scoped>
  .deal{
    width:100%;
    padding:0 15px;
    box-sizing: border-box;
  }
  .deal_title{
    margin-top:60px;
    font-size:28px;
    color:#333;
  }
  .deal_msg{
    margin-top:5px;
    color:#999;
    font-size:14px;
  }
  .password_list{
    width:100%;
    padding:0 10px;
    box-sizing: border-box;
    margin-top:80px;
    overflow: hidden;
    position: relative;
  }
  .password_list li{
    width:14.66%;
    height:30px;
    margin:0 1%;
    border-bottom:1px solid #D7D6DE;
    float:left;
    text-align: center;
  }
  .password_list .spot_act{
    border-bottom:1px solid #0D1327;
  }
  .password_list .password_list_input{
    width:100%;
    height:100%;
    position: absolute;
    top:0;
    left:0;
  }
  .list_input{
    width:100%;
    height:100%;
    opacity: 0;
  }
  .spot{
    display: inline-block;
    width:9px;
    height:9px;
    border-radius: 50%;
    background: #BFC1CE;
  }
  .spot_act .spot{
    display: inline-block;
    width:9px;
    height:9px;
    border-radius: 50%;
    background: #121D33;
  }
</style>



