<template>
    <div class="bg_f2f2f2">
      <ul class="set_list">
        <li>
          <span>版本</span>
          <span class="version">V1.0.0</span>
        </li>
      </ul>
      <p class="set_btn" @click="logout">退出登陆</p>
    </div>
</template>

<script>
    export default {
      name: "set_of",
      methods:{
        logout(){
          this.$ajax.post(this.global.sUrl+'22/sys/logout',{
            params:{
              access_token:'iaushydgaigdiagdahhjfhjdhfjkshjh'
            }
          }).then(function(res){
            console.log(res);
          }).catch(function(res){
            console.log(res);
          })
        }
      }
    }
</script>

<style scoped>
  .bg_f2f2f2{
    min-height: 100%;
    background: #f2f2f2;
    padding-top:10px;
  }
  .set_list{
    width:100%;
    box-sizing: border-box;
    padding:0 15px;
    background: #fff;
  }
  .set_list li{
    width:100%;
    /*border-bottom:1px solid #f0f0f0;*/
    font-size: 15px;
    color:#333;
    line-height: 50px;
  }
  .version{
    display: inline-block;
    float: right;
  }
  .set_btn{
    width: 100%;
    line-height: 50px;
    color:#EA624A;
    text-align: center;
    background: #fff;
    margin-top:10px;
  }
</style>



