<template>
    <div class="bg_f2f2f2">
      <ul class="record_title_list">
        <li class="list_active">全部</li>
        <li>待还款</li>
        <li>已结清</li>
      </ul>
      <ul class="record_list">
        <li v-for="data in record_list_data">
          <span class="record_list_date">{{ data.date }}</span>
          <span class="record_list_money">{{ data.money+'（'+ data.stage +'期）' }}</span>
          <i class="icon_arrow"></i>
          <span v-bind:class="{'record_list_state' : list_state_flag ,'color_4A90E2' : data.state === 1,'color_EA624A' : data.state === 2,'color_FFAA40' : data.state === 3}">{{ data.state_text }}</span>
        </li>
      </ul>
    </div>
</template>

<script>
    export default {
      name: "loan_record",
      data(){
        return{
          list_state_flag : true,
          record_list_data : [
            {date:'2018-07-12',money:'3000.00',stage:'3',state:1,state_text:'放款中'},
            {date:'2018-07-12',money:'3000.00',stage:'3',state:2,state_text:'放款失败'},
            {date:'2018-07-12',money:'3000.00',stage:'3',state:3,state_text:'放款失败'},
            {date:'2018-07-12',money:'3000.00',stage:'3',state:4,state_text:'已结清'}
          ]
        }
      }
    }
</script>

<style scoped>
  .bg_f2f2f2{
    width: 100%;
    height:100%;
    background: #f2f2f2;
  }
  .record_title_list{
    box-sizing: border-box;
    width:100%;
    border-top:1px solid #f0f0f0;
    border-bottom:1px solid #f0f0f0;
    background: #fff;
    overflow: hidden;
  }
  .record_title_list li{
    width:33.33%;
    line-height: 42px;
    height:42px;
    border-bottom:2px solid #fff;
    float:left;
    text-align: center;
    color:#333;
    font-size: 15px;
  }
  .record_title_list .list_active{
    color:#4A90E2;
    border-bottom:2px solid #4A90E2;
  }
  .record_list{
    width:100%;
    background: #fff;
    font-size: 15px;
  }
  .record_list li{
    box-sizing: border-box;
    width:100%;
    padding:0 15px;
    border-bottom:1px solid #f0f0f0;
    line-height:50px;
    position:relative;
    overflow: hidden;
  }
  .record_list li span{
    display: inline-block;
  }
  .record_list_date{
    width:90px;
  }
  .record_list_money{
    width: 100%;
    position:absolute;
    text-align: center;
    top:0;
    left:0;
  }
  .record_list_state{
    width:60px;
    float:right;
    color:#999;
  }
  .icon_arrow{
    display: inline-block;
    width:16px;
    height:16px;
    float:right;
    background: url("../assets/images/icon_next.png") no-repeat;
    background-size: 100%;
    margin-left:5px;
    margin-top:17px;
  }
  .color_4A90E2{
    color:#4A90E2;
  }
  .color_EA624A{
    color:#EA624A;
  }
  .color_FFAA40{
    color:#FFAA40;
  }
</style>



