<template>
    <div class="bg_f2f2f2">
      <ul class="repayment_list">
        <li>
          <span class="repayment_list_title">还款金额</span>
          <input type="number" v-model="defAmt" class="repayment_list_input">
        </li>
        <li class="error_msg">
          {{ error_msg }}
        </li>
        <li class="receipt_account">
          <span class="loan_list_title">收款账户</span>
          <span class="bank_name">{{ bank_name }}</span>
          <img src="../assets/images/logo_ghh.png" alt="" class="icon_bank">
          <i class="icon_arrow"></i>
        </li>
      </ul>
      <ul class="repayment_list">
        <li>
          <span class="repayment_list_title">逾期应还</span>
          <span class="repayment_list_input">{{overAmt}}元</span>
        </li>
        <li>
          <span class="repayment_list_title">本期应还</span>
          <span class="repayment_list_input">{{curAmt}}元</span>
        </li>
        <li>
          <span class="repayment_list_title">全部应还</span>
          <span class="repayment_list_input">{{totalAmt}}元</span>
        </li>
      </ul>
      <p class="repayment_btn">确认还款</p>
    </div>
</template>

<script>
  var curAmt,defAmt,overAmt,totalAmt;
    export default {
        name: "repayment",
      data(){
        return{
          curAmt:'1000',//本期应还
          defAmt:'1000',//默认还款金额
          overAmt:'1000',//逾期应还
          totalAmt:'1000',//全部应还
          bank_name:'招商银行（9215）',
          error_msg:'您当前有账单逾期，请及时还款！',

        }
      },
      created(){
        this.$ajax.post(this.global.sUrl+'18/repay/qryBeforeRepay',{
          params:{
            orderId:'12313131'
          }
        }).then(function (response) {
          console.log(response);
          curAmt = response.data.curAmt;
          defAmt = response.data.defAmt;
          overAmt = response.data.overAmt;
          totalAmt = response.data.totalAmt;

        }).catch(function (error) {
          console.log(error);
        });
      },
      methods:{
        get_data(){
          console.log(curAmt);
        }
      }
    }
</script>

<style scoped>
  .bg_f2f2f2{
    height:100%;
    background: #f2f2f2;
    overflow: hidden;
  }
  .repayment_list{
    display: block;
    margin-bottom:10px;
  }

  .repayment_list li{
    height:50px;
    padding:0 15px;
    box-sizing: border-box;
    font-size:15px;
    color:#333;
    line-height:50px;
    position:relative;
    background: #fff;
    border-top:1px solid #F0F0F0;
  }
  .receipt_account span{
    display: inline-block;
    vertical-align: middle;
  }
  .icon_arrow{
    display: block;
    width:16px;
    height:16px;
    background: url("../assets/images/icon_next.png") no-repeat;
    background-size:100%;
    position:absolute;
    right:15px;
    top:50%;
    margin-top:-8px;
  }
  .icon_bank{
    display: inline-block;
    width:20px;
    height:20px;
    float: right;
    margin:15px 6px 0 0;
  }
  .bank_name{
    display: inline-block;
    float: right;
    margin-right:22px;
  }
  .repayment_list_input{
    width:160px;
    font-size:15px;
    text-align: right;
    float: right;
    line-height: 20px;
    margin-top:15px;
  }
  .repayment_list .error_msg{
    height:35px;
    line-height:35px;
    background: #F2F2F2;
    font-size:13px;
    color:rgba(234,98,74,1);
  }
  .repayment_btn{
    display: block;
    width:90%;
    height:45px;
    margin:25px auto;
    background: #4A90E2;
    font-size:15px;
    color:#fff;
    text-align: center;
    line-height: 45px;
    border-radius:2px;
  }
</style>



