<template>
  <div>
    <p class="margin_top"></p>
    <ul class="bank_list">
      <li>
        <img src="../assets/images/logo_nh.png" alt="" class="icon_bank_logo">
        <span class="bank_name">中国农业银行</span>
      </li>
    </ul>
  </div>
</template>

<script>
    export default {
        name: "allow_bank",
      data () {
        return {
          bank_list:[],//选项弹层数据
        }
      },
      created(){
        this.$ajax.post(this.global.sUrl+'17/supportBankList',{
          params:{
            token:'2tgug23veghvegh2veghu2vghev'
          }
        }).then(function (response) {
          console.log(response);
        }).catch(function (error) {
          console.log(error);
        });
      },
      methods:{

      }
    }
</script>

<style scoped>
  .margin_top{
    width:100%;
    height:10px;
    background:rgba(242,242,242,1);
  }
  .bank_list{
    padding:0 15px;
    box-sizing: border-box;
    line-height: 60px;
    font-size: 15px;
  }
  .bank_list li{
    border-bottom:1px solid #F0F0F0;
  }
  .icon_bank_logo{
    display: inline-block;
    width:28px;
    height:28px;
    vertical-align: middle;
  }
  .bank_name{
    display: inline-block;
    vertical-align: middle;
    margin-left:15px;
  }
</style>



