<template>
  <div class="main">
    <p class="welcome_msg">{{welcome_msg}}</p>
    <p class="welcome_msg02">{{welcome_msg02}}</p>
    <ul class="login_list">
      <li>
        <input type="tel" class="login_input" maxlength="11" placeholder="请输入手机号码" v-model="mobile_number" v-on:change="check_mobile">
        <i class="clear_input" v-if="mobile_number" @click="clear_input(1)"></i>
      </li>
      <li>
        <input v-bind:type="[type_flag ? ps :  tx]" class="login_input" placeholder="请输入登陆密码" v-model="password">
        <i class="clear_input pos_right" v-if="password" @click="clear_input(2)"></i>
        <i v-bind:class="[password_flag ? sh :  hi]" @click="show_password"></i>
      </li>
    </ul>
    <p class="forget_password" @click="goto_forget">忘记密码?</p>
    <p class="login_btn login_btn_active" @click="login">登陆</p>
    <p class="goto_account" @click="goto_code_login">验证码登录</p>
    <p class="clause_text">点击登录/注册按钮代表您已阅读并同意 <span class="clause">《XXX》</span></p>
    <p class="prompt" v-if="prompt_flag"><span>{{ prompt_text }}</span></p>
  </div>
</template>

<script>
  export default {
    name: 'account_login',
    data () {
      return {
        welcome_msg:'欢迎回来！',//欢迎大标题
        welcome_msg02:'请输入您的手机号和密码进行登录操作',//提示语
        mobile_number: '',//手机号
        password:'',//登陆密码
        type_flag:true,
        ps:'password',
        tx:'text',
        password_flag:true,
        sh:'show_password',
        hi:'show_password_dis',
        prompt_text:'网络连接失败',//提示语
        prompt_flag:false,//错误提示标识
      }
    },
    methods:{
      goto_code_login () {
        this.$router.push('/code_login')
      },
      goto_forget(){
        this.$router.push('/forget_password')
      },
      clear_input(n){
        if(n === 1){
          this.mobile_number = "";
        }else if(n === 2){}
          this.password = "";
      },
      show_password(){
        this.type_flag = !this.type_flag;
        this.password_flag = !this.password_flag;

      },
      check_mobile(value){
        var that = this;
        var val = value.srcElement._value;
        if(val === '') {
          that.prompt_flag = true;
          that.mobile_number = '';
          that.prompt_text = '请输入注册手机号';
          var s = setTimeout(function(){
            that.prompt_flag = false;
            that.prompt_text = '';
          },1500)
          return false;
        }
        if(!/^0?1[3|4|5|7|8][0-9]\d{8}$/.test(val)) {
          that.prompt_flag = true;
          that.mobile_number = '';
          that.prompt_text = '手机号码输入错误';
          var s = setTimeout(function(){
            that.prompt_flag = false;
            that.prompt_text = '';
          },1500)
          return false;
        }
        return true;
      },
      login(){
        var that = this;
        if(!that.mobile_number){
          that.prompt_flag = true;
          that.prompt_text = '请输入手机号码';
          var s = setTimeout(function(){
            that.prompt_flag = false;
            that.prompt_text = '';
          },1500);
          return false;
        }
        this.$ajax.post(that.global.sUrl+'sys/appLogin',{
          params:{
            username:that.mobile_number,
            password: that.password
          }
        }).then(function (response) {
          console.log(response);

        }).catch(function (error) {
          console.log(error);
        });
      }
    }
  }
</script>

<style scoped>

  .prompt{
    width:100%;
    position:fixed;
    left:0;
    top:50%;
    color:#fff;
    font-size: 15px;
    text-align: center;
  }
  .prompt span{
    display: inline-block;
    padding:15px;
    background: #303030;
    border-radius:5px;
  }
  .forget_password{
    text-align: right;
    color:#4A90E2;
    font-size:4px;
    margin:20px 0;
  }
</style>



